import React, { useState, useEffect } from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import styled from 'styled-components';
import { email } from '@config';
import { navDelay, loaderDelay } from '@utils';
import { useStaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import { MonitorSvg } from '@components';


const StyledHeroSection = styled.section`
  ${({ theme }) => theme.mixins.flexCenter};
  flex-direction: column;
  align-items: flex-start;
  min-height: 100vh;

  h1 {
    margin: 0 0 30px 4px;
    color: var(--green);
    font-family: var(--font-mono);
    font-size: clamp(var(--fz-sm), 5vw, var(--fz-md));
    font-weight: 400;

    @media (max-width: 480px) {
      margin: 0 0 20px 2px;
    }
  }


  h3 {
    margin-top: 10px;
    color: var(--slate);
    line-height: 0.9;
  }

  p {
    margin: 20px 0 0;
    max-width: 800px;
    font-family: var(--font-nanum);
    font-size:35px;
    color: var(--lightest-slate);
  }


  .emoji {
    display: inline-block;
    vertical-align: text-top;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width:50px;
    height:50px;
    margin-left:0.5em;
    margin-top:0.1em;
  }

  .namaste {
    margin: 0 0 30px 4px;
    color: var(--green);
    font-family: var(--font-hindi);
    font-size: clamp(50px, 5vw, 50px);
    font-weight: 400;

    @media (max-width: 480px) {
      margin: 0 0 20px 2px;
    }
  }

   .email-link {
    ${({ theme }) => theme.mixins.bigButton};
    margin-top: 50px;
  }

  .grid-container {
    display: grid;
    grid-template-columns: auto auto;
  }

  .monitor {
    display: none;
  }

  @media screen and (min-width: 800px){
    .grid-container {
      display: grid;
      grid-template-columns: 70% 30%;
    }
    .monitor {
      filter: brightness(90%) saturate(90%);
      display: block;
      position:relative;
      width:45rem;
      margin-left:auto;
      padding-left:-20rem;
    }
  }
  

`;



const Hero = () => {
  const data = useStaticQuery(graphql`
    query {
      avatar: file(sourceInstanceName: { eq: "images" }, relativePath: { eq: "namaste.png" }) {
        childImageSharp {
          fluid(maxWidth: 500, traceSVG: { color: "#64ffda" }) {
            ...GatsbyImageSharpFluid_withWebp_tracedSVG
          }
        }
      }
    }
  `);

  const [isMounted, setIsMounted] = useState(false);

  useEffect(() => {
    const timeout = setTimeout(() => setIsMounted(true), navDelay);
    return () => clearTimeout(timeout);
  }, []);


  const one = <br></br>;

  const two = <h1 className="namaste"> |
  <Img fluid={data.avatar.childImageSharp.fluid} alt="Avatar" className="emoji" />
  </h1>;

  // const two = <h2 className="big-heading">Ramit Koul</h2>;



  // const three = <h3 className="big-heading">I build things for the web.</h3>;

  // const three=<br></br>;

  const four = (
    <p>
      I'm <b>Ramit Koul</b>, a senior year student based in Punjab, India pursuing my B.E degree from {' '}
      <a href="https://thapar.edu" target="__blank">TIET, Patiala</a>. I enjoy building things that live on the internet.
    </p>
  );

  const five = (
    <a href={`mailto:${email}`} className="email-link">
      Get In Touch
    </a>
  );
  const svg=(<div className="monitor"><MonitorSvg /></div>);

  const items = [two,four, five];
  const svgs = [svg];

  return (
    <StyledHeroSection>
      <div className="grid-container">
        <div className="grid-item">
          <div>{one}</div>
          <div>{one}</div>
          <TransitionGroup component={null}>
            {isMounted &&
              items.map((item, i) => (
                <CSSTransition key={i} classNames="fadeup" timeout={loaderDelay}>
                  <div style={{ transitionDelay: `${i + 1}00ms` }}>{item}</div>
                </CSSTransition>
              ))}
          </TransitionGroup>
        </div>

        <TransitionGroup component={null}>
            {isMounted &&
              svgs.map((item, i) => (
                <CSSTransition key={i} classNames="fadeup" timeout={loaderDelay}>
                  <div style={{ transitionDelay: `${i + 1}00ms` }}>{item}</div>
                </CSSTransition>
              ))}
          </TransitionGroup>
        
      </div>
    </StyledHeroSection>
  );
};

export default Hero;
